import React from 'react';
// import { Link } from 'react-router-dom';

const PortfolioDetail = (props) => {
    return (
        <div className="col-sm-12 col-md-4 col-lg-4 content-right">
            <img className="img-fluid img-portfolio" src={props.src} alt="ayoti" />
            <p className="font-size-19 font-family-lato font-weight-900 padding-top-20">
                {props.children}
            </p>
        </div>
    )
};

export default PortfolioDetail;