import React from "react";
import { NavLink } from "react-router-dom";
const Header = () => {
  return (
    <nav className="navbar navbar-expand-lg navbar-light">
      <div className="my-0 mr-md-auto font-weight-normal title top-left">
        athmafarhan
      </div>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNavDropdown"
        aria-controls="navbarNavDropdown"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div
        className="navbar-collapse collapse w-100 order-3 dual-collapse2"
        id="navbarNavDropdown"
      >
        <ul className="navbar-nav ml-auto title">
          <NavLink
            exact
            to="/"
            style={{ textDecoration: "none" }}
            activeStyle={{
              borderBottom: "4px solid",
              borderBottomColor: "black"
            }}
          >
            <li className="nav-item active">
              <div className="nav-link">
                profil<span class="sr-only">(current)</span>
              </div>
            </li>
          </NavLink>
          <NavLink
            to="/portfolio"
            style={{ textDecoration: "none" }}
            activeStyle={{
              borderBottom: "4px solid",
              borderBottomColor: "black"
            }}
          >
            <li className="nav-item active">
              <div className="nav-link">
                portfolio<span class="sr-only">(current)</span>
              </div>
            </li>
          </NavLink>
          <NavLink
            to="/kontak"
            style={{ textDecoration: "none" }}
            activeStyle={{
              borderBottom: "4px solid",
              borderBottomColor: "black"
            }}
          >
            <li className="nav-item active">
              <div className="nav-link">
                kontak<span class="sr-only">(current)</span>
              </div>
            </li>
          </NavLink>
        </ul>
      </div>
    </nav>
  );
};

export default Header;
